    #header {
        #logo {
            svg {
                #nome {
                    fill: #ffffff ;
                }
            }

            &.home svg #riga {
                fill: #f7a000;
            }

            &:hover svg #background-splash {
                fill: #a100a0;
            }
        }
    }
}