#header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    #logo {
        width: 100%;
        height: 2rem;

        svg {
            #nome {
               fill: #000000;
            }

            #background-splash {
                transition: .2s;
                fill: transparent;
            }

            #riga {
                fill: transparent;
            }
        }

        &:hover svg #background-splash {
            fill: #ffd500;
        }

        &.home {
            svg #riga {
                fill: #af0303;
            }
            
            &:hover svg #background-splash {
                    fill: transparent;
                }
            }
        
    }

    a {
        text-decoration: underline;
        text-decoration-thickness: 0.1rem;

        &:visited {
            color: $link;
        }
    }

    #in-lettura a {
        text-decoration: none;
    }
}